.page-header__logo-image {
    margin: 7px 14px 0;
}

@media (min-width: $tablet-width) {
    .page-header {
        grid-area: header;
        display: grid;
        grid-template-columns: 1fr minmax(auto, 300px) minmax(auto, 900px) 1fr;
        align-items: center;
        justify-content: space-between;
    }

    .page-header__logo{
        grid-column: 2/3;
    }
}

@media (min-width: $desktop-width) {
    .page-header__logo {
        padding-top: 35px;
    }
}