.example {
    margin-top: 40px;
    padding: 20px;
    background: $basic-extra-light-gray;
}

.example__title {
    font-family: $special-font;
    font-size: 36px;
    font-weight: normal;
    line-height: 1;
    margin: 8px 0;
}

.example__text {
    font-size: 14px;
    line-height: 1.3;
    margin-top: 35px;
}

@media (min-width: $tablet-width) {
    .example {
        padding: 20px 0;
        background-color: $basic-white;
        border-top: 1px solid $special-light-gray;
        margin-top: 16px;
    }

    .example__header {
        padding: 20px;
    }

    .example__title {
        font-size: 60px;
        margin: 23px 0;
    }

    .example__text {
        font-size: 16px;
        line-height: 1.5;
        margin-top: 64px;
    }
}

@media (min-width: $desktop-width) {
    .example {
        @include wrapper(40px);
    }
}
