.benefits__title {
    font-family: $special-font;
    font-size: 35px;
    font-weight: normal;
    line-height: 1;
    padding: 0px 16px;
    margin: 28px 0 23px;
}

.benefits__list {
    @include reset-list
}

.benefits__item {
    display: flex;
    padding: 18px 16px 2px;

    &::before{
        content: " ";
        width: 132px;
        height: 60px;
        background: $basic-green;
    }
}

.benefits__count {
    display: none;
}

.benefits__description {
    line-height: 1.2;
    margin: 0;
    padding-left: 18px;
    font-size: 16px;
}

@media (min-width: $tablet-width) {
    .benefits__item {
        flex-direction: column;
        min-height: 240px;
        padding: 45px 16px 32px;
        position: relative;

        &::before{
            width: 80px;
            height: 80px;
        }
    }

    .benefits__title {
        font-size: 60px;
        margin: 47px 0 26px;
    }

    .benefits__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .benefits__count {
        display: block;
        position: absolute;
        top: 134px;
        left: 34%;
        font-family: $special-font;
        font-size: 280px;
        color: $basic-extra-light-gray;
        z-index: -1;
    }

    .benefits__description {
        padding: 0;
        width: 64%;
        font-size: 17px;
        line-height: 1.4;
        margin-top: 33px;
    }
}

@media (min-width: $desktop-width) {
    .benefits {
        @include wrapper;
    }

    .benefits__list {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        gap: 30px;
    }

    .benefits__item {
        padding: 45px 25px 32px;
    }

    .benefits__description {
        width: 100%;
    }
}