.programs__list {
    @include reset-list
}

.programs__item {
    position: relative;
    background: $basic-extra-light-gray;
    margin: 0 14px 22px;
    padding: 20px;

    &::before{
        content: " ";
        width: 52px;
        height: 52px;
        background: $basic-green;
        border-radius: 26px;
        position: absolute;
        top: 25px;
        left: 22px;
    }
}

.programs__title {
    font-family: $special-font;
    font-size: 24px;
    font-weight: normal;
    line-height: 1;
    text-transform: uppercase;
    padding: 17px 0 22px 72px;
    margin: 0;
}

.programs__text {
    font-size: 14px;
    line-height: 1.2;
    margin: 14px 0;
}

.programs__link {
    display: block;
    width: 100%;
    font-family: $special-font;
    font-size: 16px;
    color: $special-dark-gray;
    text-transform: uppercase;
    text-decoration: none;
    padding-top: 14px;
    border-top: 1px solid $basic-gray;
}

@media (min-width: $tablet-width) {
    .programs__item {
        padding: 54px;
        &::before{
            width: 200px;
            height: 200px;
            border-radius: 100px;
            top: 92px;
            right: 62px;
            left: auto;
        }
    }

    .programs__title {
        font-size: 36px;
        padding: 22px 0;
    }

    .programs__text {
        font-size: 16px;
        line-height: 1.5;
        margin: 2px 0 40px;
        max-width: 65%;
    }

    .programs__link {
        font-size: 19px;
        border-top: none;
    }
}

@media (min-width: $desktop-width) {
    .programs {
        @include wrapper(74px);
    }

    .programs__list {
        display: grid;
        grid-column-gap: 80px;
        grid-template-columns: 1fr 1fr;
    }

    .programs__item {
        margin: 0;

        &::before{
            width: 100px;
            height: 100px;
            border-radius: 50px;
            top: 47px;
            left: 50px;
            right: auto;
        }
    }

    .programs__title {
        padding: 22px 0 20px 158px;
    }

    .programs__text {
        margin: 54px 0 16px;
        max-width: 100%;
    }
}