.slogan {
    font-family: $special-font;
}

.slogan__wrapper {
    margin: 0 auto;
    max-width: 300px;
    padding: 4px 24px 24px;
}

.slogan__title {
    font-size: 36px;
    line-height: 1;
    font-weight: normal;
    margin: 22px 0;
}

.slogan__text {
    font-weight: normal;
    font-size: 16px;
    text-transform: uppercase;
}

.slogan__food {
    padding-top: 18px;
}

.slogan__button {
    font-family: $special-font;
    line-height: 1;
    font-size: 13px;
    text-transform: uppercase;
    color: $basic-white;
    background: $basic-green;
    display: block;
    width: 100%;
    border: none;
    padding: 14px;
    cursor: pointer;

    &:hover {
        background-color: $special-green;
    }
}

@media (max-width: $only-mobile-width) {
    .slogan {
        color: $basic-white;
        text-align: center;
    
        background-image: url("../img/bg-cat.jpg"),
        linear-gradient(to bottom, $special-dark-green, $special-dark-green 60%, $basic-white 50%, $basic-white 100%);
        background-repeat: no-repeat;
        background-position: center top;
        background-size: 320px auto, 100% auto;

            @media (min-resolution: $retina-dppx) {
                background-image: url("../img/bg-cat@2x.jpg"),
                linear-gradient(to bottom, $special-dark-green, $special-dark-green 60%, $basic-white 50%, $basic-white 100%);
            }
    }
}

@media (min-width: $tablet-width) {
    .slogan {
        position: relative;
        text-align: left;
        padding: 8px 84px;
        color: $basic-black;
        height: 784px;
    }

    .slogan__wrapper {
        max-width: 530px;
    }

    .slogan__title {
        font-size: 58px;
        margin: 36px 0;
    }

    .slogan__text {
        font-size: 18px;
    }

    .slogan__button {
        position: absolute;
        top: 272px;
        font-size: 20px;
        width: 246px;
    }
}

@media (min-width: $desktop-width) {
    .slogan {
        height: 590px;  
    }

    .slogan__wrapper {
        max-width: 1062px;  
    }

    .slogan__title {
        padding-top: 50px;
        max-width: 44%;
        margin-bottom: 42px;
    }

    .slogan__text {
        display: block;
        margin-bottom: 48px;
    }
    .slogan__food {
        position: absolute;
        left: calc(50% - 156px);
        top: 0px;
    }

    .slogan__button {
        position: static;
        font-size: 20px;
        padding: 16px 27px;
    }
}