.stats {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;
    margin-top: 26px;
}

.stats__item {
    border: 1px solid $basic-gray;
    border-radius: 2px;
    text-align: center;
    position: relative;
    padding-bottom: 16px;
}

.stats__value {
    font-family: $special-font;
    font-size: 25px;
    text-transform: uppercase;
    padding-top: 12px;
}

.stats__definition {
    position: absolute;
    bottom: -16px;
    left: 50%;
    transform: translateX(-50%);
    background: $basic-extra-light-gray;
    width: 74px;
    font-size: 13px;
    padding: 0 14px;
    line-height: 1;
}

.stats__itog {
    font-family: $special-font;
    text-transform: uppercase;
    font-size: 14px;
    text-align: center;
    grid-column: span 2; 
}

@media (min-width: $tablet-width) {
    .stats {
        grid-template-columns: minmax(auto, 162px) minmax(auto, 162px) 1fr;
        gap: 44px;
        margin: 18px 20px 22px;
    }

    .stats__item {
        padding-bottom: 24px;
    }

    .stats__value {
        font-size: 30px;
        padding-top: 18px;
    }
    
    .stats__definition {
        background: #ffffff;
        width: auto;
        white-space: nowrap;
        bottom: -7px;
        padding: 0 8px;
    }
    .stats__itog {
        grid-column: inherit;
        padding-top: 8px;
        font-size: 21px;
        text-align: right;
    }

    .stats__itog-value {
        display: block;
    }
}